
import { defineComponent, ref } from 'vue'

export default defineComponent({
  name: 'SearchComponent',

  setup(__, { emit }) {
    let userName = ref<string>()

    const handleSearchUer = (): void => {
      emit('search-user', userName.value)
    }

    return {
      userName,
      handleSearchUer,
    }
  },
})
